$c_orange: #EE4F16;
$c_black: #262523;
$c_grey: #4d4a46;
$c_dark_grey: #353330;
$c_light_grey: #85807a;
$c_blue: #00798C;
$c_white: #F7F4ED;
$c_red: tomato;

$t_window_size: 46vh;

@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap');

* {
    margin: 0;
    padding: 0;
    font-family: 'Roboto Mono', monospace;
    box-sizing: border-box;
}

#focus {
    width: calc(600px + 10vw);
    height: calc($t_window_size + 30vh);
    margin-top: calc(-1 * (100vh - $t_window_size - 20vh));

    .top_row {
        ul {
            .unfocues_file {
                color: $c_light_grey;
                background-color: $c_dark_grey;

            }

            .focues_file {
                color: $c_white;
                background-color: $c_blue;
            }
        }
    }
}

.cards {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;

    .t_window_div {
        width: 600px;
        height: $t_window_size;
        margin: 10px;
        border: 2px solid $c_light_grey;
        background-color: $c_black;
        border-radius: 3px;
        font-size: 16px;
        transition: width 500ms, height 500ms, margin 500ms;
        overflow-y: none;

        .top_row {
            width: 100%;
            height: 25px;
            background-color: $c_dark_grey;
            float: right;
            display: flex;
            align-items: center;
            border-bottom: 1px solid $c_grey;

            ul {
                list-style: none;

                .unfocues_file {
                    color: $c_white;
                    background-color: $c_blue;
                    border-radius: 2px;
                }

                .focues_file {
                    color: $c_light_grey;
                    border-radius: 2px;
                    background-color: $c_dark_grey;
                }

                li {
                    font-size: 20px;
                    float: left;
                    margin-left: 10px;
                    font-size: 12px;
                    color: $c_light_grey;
                }
            }
        }

        .title_bar {
            border-radius: 3px;
            height: 30px;
            width: 100%;
            background-color: $c_grey;
            color: $c_white;
            display: flex;
            justify-content: space-between;
            border-bottom: 1px solid $c_light_grey;

            p {
                font-family: 'Roboto Mono', monospace;
                font-size: 16px;
                margin-left: 5px;
                margin-right: 5px;
                margin-top: 2px;
            }

            .icons {
                height: 100%;
                width: 100px;
                display: flex;
                align-items: center;
                justify-content: center;

                .button {
                    height: 90%;
                    width: 30px;
                    margin: 2px;
                    background-size: 15px;
                    background-position: center;
                    background-repeat: no-repeat;
                    border-radius: 2px;
                    transition: background-color 500ms;
                }

                .button:hover {
                    background-color: $c_orange;
                }

                .minimalize {
                    background-image: url(../imgs/minim.png);
                }

                .fullscreen {
                    background-image: url(../imgs/fulls.png);
                }

                .close {
                    background-image: url(../imgs/close.png);
                }
            }
        }

        .line_numbers {
            margin-top: 1px;
            height: calc(100% - 30px - 25px);
            width: 30px;
            float: left;
            border-right: 1px solid $c_grey;
            background-color: $c_dark_grey;
            overflow-y: hidden;

            ol {

                list-style-type: decimal;
                list-style-position: inside;

                li {
                    margin-right: 2px;
                    text-align: right;
                    display: block;
                    color: $c_light_grey;
                    counter-increment: customlistcounter;

                }

                li:before {
                    content: counter(customlistcounter) " ";
                    counter-increment: item;
                }
            }
        }

        h1 {
            color: $c_blue;
            font-family: 'Cutive Mono', monospace;
            font-size: 20px;
        }

        p {
            color: $c_white;
        }

        a {
            color: $c_orange;
        }


    }
}

.heading {
    background-color: $c_dark_grey;
    height: calc(100vh - $t_window_size - 80px);
    width: 100%;
    transition: left .5s;
    transition: right .5s;
    position: relative;
    background-image: url("../imgs/main_background.png");

    .rust_log {
        position: absolute;
        width: 200px;
        height: 200px;
    }

    .rust_log1 {
        left: -20px;
        bottom: -29px;
        transform: rotate(20deg);
        transform: scale(200px);
    }

    .rust_log2 {
        right: -20px;
        top: 20px;
        transform: rotate(-16deg);

    }

    .heading-container {
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        transition: left .5s;
        left: calc(50% - calc(calc(55vh - 4vw) / 2));
        top: calc(50% - calc(23vh / 2));
        width: calc(55vh - 4vw);
        height: 23vh;

        h1 {
            color: $c_white;
            font-size: calc(25vh - 4vw);

            .cursor {
                width: calc(55vh - 4vw);
                height: 23vh;
                position: absolute;
                left: 90%;
                z-index: 1;
                animation: blink 1.1s infinite 1s;
            }

            @keyframes blink {
                0% {
                    opacity: 0;
                }

                49% {
                    opacity: 0;

                }

                50% {
                    opacity: 1;
                }
            }
        }

        h2 {
            color: $c_white;
            font-size: 2vh;
        }
    }
}

body {
    background-color: $c_black;
    width: 100%;
    height: 100%;
    overflow-x: hidden;

    .navbar {
        font-family: 'Roboto Mono', monospace;
        position: relative;
        width: 100%;
        height: 60px;
        background-color: $c_grey;
        display: flex;
        justify-content: end;
        align-items: center;

        ul {
            list-style: none;

            li {
                font-size: 20px;
                float: right;
                margin-right: 30px;

                a {
                    color: $c_white;
                    text-decoration: none;
                }
            }
        }
    }

    main {
        width: 100%;
        height: 100%;

        .index {
            position: absolute;
            background-color: $c_black ;
            width: 100%;

            .navbar {
                margin-top: 0;
                font-family: 'Roboto Mono', monospace;
                width: 100%;
                height: 60px;
                position: absolute;
                top: 0;
                left: 0;
                background-color: $c_grey;
                display: flex;
                justify-content: end;
                align-items: center;

                ul {
                    list-style: none;

                    li {
                        font-size: 20px;
                        float: right;
                        margin-right: 30px;

                        a {
                            color: #F7F4ED;
                            text-decoration: none;
                        }
                    }
                }
            }
        }

        .status {
            position: absolute;
            background-color: $c_orange;
            width: 100%;

        }

        .learn {
            position: absolute;
            background-color: $c_white;
            width: 100%;
        }

        .news {
            position: absolute;
            background-color: $c_red;
            width: 100%;
            height: calc(100% - 60px);

            #news_div {
                width: 100%;
                height: calc(100% - 20px);
            }
        }
    }
}

@media only screen and (max-width: 1200px) {

    .cards {
        position: relative;
        flex-wrap: wrap;
    }

    #focus {
        width: 600px;
        height: calc($t_window_size + 30vh);
        margin-top: 0;
    }

}